.textfld {
  border-radius: 4px;
  /* height: 50px; */
  /* width: 220px; */
  background-color: rgb(255 255 255 / 7%);
}

.textfld .MuiOutlinedInput-root {
  border: 1px solid #333353;
}
.textfld .Mui-focused {
  border: 1px solid #333353;
}
.textfld .MuiInputBase-input {
  color: white;
  font-weight: 400;
  font-size: 16px;
  line-height: 31px;
  font-family: Roboto;
}
.textfld .MuiOutlinedInput-input {
  padding: 15px 10px 15px 23px;
}
/* .textfld .MuiOutlinedInput-input:-webkit-autofill {
  -webkit-box-shadow: 'none';
  display: 'none';
  -webkit-box-sizing: ;
} */
.textfld .MuiInputBase-input[type='number']::-webkit-inner-spin-button,
.textfld .MuiInputBase-input[type='number']::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.textfld .MuiOutlinedInput-notchedOutline {
  border: none;
}
.textfld .MuiInputBase-input::placeholder {
  color: #e34646 !important;
  font-size: 13px;
}
